.header {
  background-image: url("../img/welcome-desktop.jpg");
  background-color: $color-prussian-blue;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding: 35px 7.5%;


  @media (min-width: 1024px) {
    min-height: 650px;
    box-shadow: 0 4px 4px $color-transparent;
    z-index: 2;
  }

  @media screen and (max-width: 1023px) {
    background-image: url("../img/welcome-tablet.jpg");
    padding: 30px 5.9%;
  }

  @media screen and (min-width: 768px) and (max-width: 1023px) {
    min-height: 638px;
  }

  @media screen and (max-width: 767px) {
    background-image: url("../img/welcome-mobile.webp");
    padding: 20px 5%;
    min-height: 500px;
  }
}

.header__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  margin: 0 auto;
  box-sizing: border-box;

  @media (min-width: 1024px) {
    max-width: 1160px;
  }

  @media screen and (max-width: 1023px) {
    width: 678px;
  }

  @media screen and (max-width: 767px) {
    width: calc(290px + (100vw - 320px) * (290 - 694) / (320 - 767));
    max-width: 694px;
  }
}

.header__first-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  align-items: center;

  @media (min-width: 1024px) {
    max-width: 1160px;
  }

  @media screen and (max-width: 767px) {
    width: calc(290px + (100vw - 320px) * (290 - 694) / (320 - 767));
    max-width: 694px;
  }
}

.header__img {
  @media (min-width: 1024px) {
    min-width: 70px;
    min-height: 38px;
  }

  @media screen and (max-width: 1023px) {
    width: 38px;
    height: 20px;
  }

  @media screen and (max-width: 767px) {
    max-width: 42px;
    max-height: 22px;
  }
}

.header__img.is-invalid {
  @media screen and (max-width: 767px) {
    display: none;
  }
}
